"use client"

import { useEffect, useMemo, useState } from "react"
import CategoryForm from "@/components/category-form"
import AuditItemForm from "@/components/audit-item-form"
import AuditTable from "@/components/audit-table"
import DateFilter from "@/components/date-filter"
import { exportAuditToPDF } from "@/components/AuditPDFDocument"
import { useStore } from "@/store/useStore" 
import type { DateFilter as DateFilterType } from "@/lib/types"

export default function HousekeepingAudit() {
  const {
    categories,
    dailyAreas,
    loadingCategories,
    loadingDailyAreas,
    fetchCategories,
    fetchDailyAreas,
    createCategory,
    addDailyArea,
  } = useStore()

  const [showCategoryForm, setShowCategoryForm] = useState(false)
  const [showItemForm, setShowItemForm] = useState(false)
  const [selectedCategoryId, setSelectedCategoryId] = useState<string | null>(null)
  const [dateFilter, setDateFilter] = useState<DateFilterType>({})
  const [exporting, setExporting] = useState(false)
  const [refreshingCategoryId, setRefreshingCategoryId] = useState<string | null>(null)

  useEffect(() => {
    fetchCategories()
    fetchDailyAreas()
  }, [fetchCategories, fetchDailyAreas])

  // Transform dailyAreas ‚Üí table rows (one row per dateStatus)
  const transformedCategories = useMemo(() => {
    return categories.map(cat => {
      const items = dailyAreas
        .filter(area => area.categoryId === cat._id)
        .flatMap(area =>
          (area.dateStatuses ?? []).map(ds => ({
            _id: ds._id,
            categoryId: cat._id,
            element: area.areaName,
            status: ds.status === "ok" ? "checked" : ds.status === "notok" ? "crossed" : "pending",
            createdAt: ds.date || new Date().toISOString(),
            command: ds.command || "",
            actionTimeframe: ds.actionTimeframe || "open",
            followUp: ds.followUp || "",
          }))
        )

      return {
        id: cat._id,
        name: cat.name,
        createdAt: cat.createdAt || new Date().toISOString(),
        items,
      }
    })
  }, [categories, dailyAreas])

  const filteredCategories = useMemo(() => {
    if (!dateFilter.day && !dateFilter.month && !dateFilter.year) return transformedCategories

    return transformedCategories
      .map(cat => ({
        ...cat,
        items: cat.items.filter(item => {
          const d = new Date(item.createdAt)
          return (
            (!dateFilter.day || d.getDate() === dateFilter.day) &&
            (!dateFilter.month || d.getMonth() + 1 === dateFilter.month) &&
            (!dateFilter.year || d.getFullYear() === dateFilter.year)
          )
        })
      }))
      .filter(cat => cat.items.length > 0)
  }, [transformedCategories, dateFilter])

  const stats = useMemo(() => {
    const all = filteredCategories.flatMap(c => c.items)
    return { total: all.length, checked: all.filter(i => i.status === "checked").length }
  }, [filteredCategories])

  // Add Category
  const handleAddCategory = async (name: string) => {
    const res = await createCategory({ name, description: "" })
    if (!res.error && res.data) {
      setSelectedCategoryId(res.data._id || res.data.id)
      setShowCategoryForm(false)
      setShowItemForm(true)
    }
  }

  // Add Items
  const handleAddItems = async (elements: string[]) => {
    if (!selectedCategoryId) return
    setRefreshingCategoryId(selectedCategoryId)

    try {
      const promises = elements.map(el =>
        addDailyArea({ category: selectedCategoryId, areaName: el.trim() })
      )
      await Promise.all(promises)
      await fetchDailyAreas()
    } catch (error) {
      console.error(error)
    } finally {
      setRefreshingCategoryId(null)
      setShowItemForm(false)
      setSelectedCategoryId(null)
    }
  }

  // Toggle Status
  const handleToggleStatus = async (
    categoryId: string,
    itemId: string,
    newStatus: "checked" | "crossed"
  ) => {
    setRefreshingCategoryId(categoryId)
    const targetStatus = newStatus === "checked" ? "ok" : "notok"

    // Find the dailyArea + dateStatus
    const dailyArea = dailyAreas.find(area =>
      (area.dateStatuses ?? []).some(ds => ds._id === itemId)
    )
    if (!dailyArea) return console.error("dailyArea not found")

    const dateStatus = (dailyArea.dateStatuses ?? []).find(ds => ds._id === itemId)
    if (!dateStatus) return console.error("dateStatus not found")

    // Call store function
    await useStore.getState().updateDailyStatus({
      areaId: dailyArea._id,
      date: dateStatus.date || new Date().toISOString().substring(0, 10),
      status: targetStatus,
      command: dateStatus.command,
      actionTimeframe: dateStatus.actionTimeframe || "open",
      completed: targetStatus === "ok",
      signature: "Hasna",
      followUp: dateStatus.followUp || "",
    })

    await fetchDailyAreas()
    setRefreshingCategoryId(null)
  }

  const openItemForm = (categoryId: string) => {
    setSelectedCategoryId(categoryId)
    setShowItemForm(true)
  }

  if (loadingCategories || loadingDailyAreas) {
    return <div className="p-8 text-center">Loading audit data...</div>
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-bold">Housekeeping Audit List</h2>
        <div className="flex gap-3">
          <DateFilter onFilterChange={setDateFilter} activeFilter={dateFilter} />
          <button onClick={() => setShowCategoryForm(true)}>+ Add Category</button>
        </div>
      </div>

      {/* Forms */}
      {showCategoryForm && <CategoryForm onSubmit={handleAddCategory} onCancel={() => setShowCategoryForm(false)} />}
      {showItemForm && selectedCategoryId && (
        <AuditItemForm
          categoryName={categories.find(c => c._id === selectedCategoryId)?.name || ""}
          onAdd={handleAddItems}
          onCancel={() => setShowItemForm(false)}
        />
      )}

      {/* Tables */}
      {filteredCategories.length === 0 ? (
        <p>No items found</p>
      ) : (
        filteredCategories.map(cat => (
          <AuditTable
            key={cat.id}
            items={cat.items}
            isLoading={refreshingCategoryId === cat.id}
            categoryId={cat.id}
            categoryName={cat.name}
            onToggleStatus={handleToggleStatus}
            onEdit={(item) => console.log("Edit", item)}
            onUpdateDetails={(itemId, updates) => console.log("Update", itemId, updates)}
          />
        ))
      )}
    </div>
  )
}
