"use client";

import { create } from "zustand";
import { persist } from "zustand/middleware";
import { toast } from "sonner";
import { baseUrl } from "@/api/const";

export type AdminType = {
  _id: string;
  name: string;
  email: string;
  role: string;
  image?: string;
  createdAt?: string;
  updatedAt?: string;
};

type AdminState = {
  admin: AdminType | null;
  isAuth: boolean;
  loading: boolean;
  error: string | null;
  admins: AdminType[];
  totalAdmins: number;
  currentPage: number;
  totalPages: number;
};

type AdminActions = {
  login: (credentials: { email: string; password: string }) => Promise<boolean>;
  logout: () => Promise<void>;
  checkAuth: () => Promise<boolean>;
  fetchAdmins: (page?: number, limit?: number, search?: string) => Promise<void>;
  addAdmin: (data: { name: string; email: string; password: string; image?: string }) => Promise<boolean>;
  forgotPassword: (email: string) => Promise<boolean>;
  resetPassword: (token: string, password: string) => Promise<boolean>;
  fetchDashboardStatus: () => Promise<any>;
  clearError: () => void;
};

export const useAdminStore = create<AdminState & AdminActions>()(
  persist(
    (set, get) => ({
      admin: null,
      isAuth: false,
      loading: false,
      error: null,
      admins: [],
      totalAdmins: 0,
      currentPage: 1,
      totalPages: 0,

      clearError: () => set({ error: null }),

      // ================= LOGIN =================
  login: async (credentials) => {
  console.log("‚ö° LOGIN ATTEMPT STARTED", credentials);

  set({ loading: true, error: null });

  try {
    const res = await fetch(`${baseUrl}/admin/admin-login`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      credentials: "include",  // üëà cookie support
      body: JSON.stringify(credentials),
    });

    console.log("‚¨Ö Raw Response:", res);

    const data = await res.json();
    console.log("üì• Parsed Response JSON:", data);

    if (!res.ok) {
      console.log("‚ùå Login failed:", data.message);
      toast.error(data.message || "Login failed");

      set({ loading: false, error: data.message });
      return false;
    }

    console.log("‚úÖ Cookie-based login success!");
    set({
      isAuth: true,
      admin: data.admin || null, // if backend sends admin
      error: null,
      loading: false,
    });

    toast.success("Login successful");
    return true;

  } catch (err) {
    console.log("üî• NETWORK ERROR:", err);

    toast.error("Network error");
    set({ loading: false, error: "Network error" });
    return false;
  }
},



      // ================= CHECK AUTH =================
      checkAuth: async () => {
        const token = localStorage.getItem("adminToken");
        if (!token) {
          set({ isAuth: false, admin: null });
          return false;
        }

        try {
          const res = await fetch(`${baseUrl}/dashbord-status`, {
            method: "GET",
            headers: { Authorization: `Bearer ${token}` },
          });

          if (!res.ok) {
            localStorage.removeItem("adminToken");
            set({ isAuth: false, admin: null });
            return false;
          }

          const data = await res.json();
          set({ isAuth: true, admin: data.admin || null });
          return true;
        } catch {
          set({ isAuth: false, admin: null });
          return false;
        }
      },

      // ================= LOGOUT =================
      logout: async () => {
        set({ loading: true });
        try {
          localStorage.removeItem("adminToken");
          set({ isAuth: false, admin: null, admins: [] });
          toast.success("Logged out successfully");
        } catch {
          toast.error("Logout failed");
        } finally {
          set({ loading: false });
        }
      },

      // ================= FETCH ADMINS =================
      fetchAdmins: async (page = 1, limit = 10, search = "") => {
        set({ loading: true, error: null });
        const token = localStorage.getItem("adminToken");
        if (!token) {
          set({ isAuth: false });
          return;
        }

        try {
          let url = `${baseUrl}/getalladmins?page=${page}&limit=${limit}`;
          if (search) url += `&search=${encodeURIComponent(search)}`;

          const res = await fetch(url, {
            headers: { Authorization: `Bearer ${token}` },
          });

          const data = await res.json();

          if (!res.ok || !data.success) {
            throw new Error(data.message || "Failed to fetch admins");
          }

          set({
            admins: data.admins,
            totalAdmins: data.total,
            currentPage: data.currentPage,
            totalPages: data.totalPages,
          });
        } catch (err: any) {
          toast.error(err.message || "Failed to load admins");
          set({ error: err.message });
        } finally {
          set({ loading: false });
        }
      },

      // ================= ADD ADMIN =================
      addAdmin: async (data) => {
        set({ loading: true, error: null });
        const token = localStorage.getItem("adminToken");
        if (!token) {
          set({ isAuth: false });
          return false;
        }

        try {
          const res = await fetch(`${baseUrl}/addAdmin`, {
            method: "POST",
            headers: { "Content-Type": "application/json", Authorization: `Bearer ${token}` },
            body: JSON.stringify(data),
          });

          const result = await res.json();

          if (!res.ok || !result.success) {
            toast.error(result.message || "Failed to add admin");
            set({ error: result.message });
            return false;
          }

          toast.success("Admin added successfully");
          get().fetchAdmins();
          return true;
        } catch {
          toast.error("Network error");
          set({ error: "Network error" });
          return false;
        } finally {
          set({ loading: false });
        }
      },

      // ================= DASHBOARD =================
      fetchDashboardStatus: async () => {
        const token = localStorage.getItem("adminToken");
        if (!token) {
          set({ isAuth: false });
          return null;
        }

        try {
          const res = await fetch(`${baseUrl}/dashbord-status`, {
            headers: { Authorization: `Bearer ${token}` },
          });

          if (!res.ok) throw new Error("Unauthorized");

          return await res.json();
        } catch (err) {
          console.error("Dashboard fetch failed", err);
          toast.error("Failed to load dashboard data");
          return null;
        }
      },

      // ================= FORGOT & RESET =================
      forgotPassword: async (email) => {
        set({ loading: true, error: null });
        try {
          const res = await fetch(`${baseUrl}/forgot-password`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ email }),
          });
          const data = await res.json();
          if (!res.ok) {
            toast.error(data.message || "Failed to send reset link");
            return false;
          }
          toast.success("Reset link sent to email");
          return true;
        } catch {
          toast.error("Network error");
          return false;
        } finally {
          set({ loading: false });
        }
      },

      resetPassword: async (token, password) => {
        set({ loading: true, error: null });
        try {
          const res = await fetch(`${baseUrl}/reset-password/${token}`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ password }),
          });
          const data = await res.json();
          if (!res.ok) {
            toast.error(data.message || "Invalid or expired token");
            return false;
          }
          toast.success("Password reset successful");
          return true;
        } catch {
          toast.error("Network error");
          return false;
        } finally {
          set({ loading: false });
        }
      },
    }),
    {
      name: "admin-auth-storage",
      partialize: (state) => ({ isAuth: state.isAuth }),
    }
  )
);
