import axios from "axios";

import { baseUrl } from "./const";


// Get token from localStorage
const getToken = () => localStorage.getItem("adminToken");

// Axios instance with default headers
const axiosInstance = axios.create({
  baseURL: baseUrl,
  headers: {
    "Content-Type": "application/json",
  },
});

// Request interceptor to attach token
axiosInstance.interceptors.request.use((config) => {
  const token = getToken();
  if (token) {
    config.headers = {
      ...config.headers,
      Authorization: `Bearer ${token}`,
    };
  }
  return config;
});

// --- API Helpers ---
export const getRequest = async (url: string) => {
  try {
    const res = await axiosInstance.get(url, { withCredentials: true });
    return res.data;
  } catch (err: any) {
    return { error: true, message: err.response?.data?.message || err.message };
  }
};

export const postRequest = async ({ url, data }: { url: string; data?: any }) => {
  try {
    const res = await axiosInstance.post(url, data, { withCredentials: true });
    return res.data;
  } catch (err: any) {
    return { error: true, message: err.response?.data?.message || err.message };
  }
};

export const patchRequest = async ({ url, data }: { url: string; data?: any }) => {
  try {
    const res = await axiosInstance.patch(url, data, { withCredentials: true });
    return res.data;
  } catch (err: any) {
    return { error: true, message: err.response?.data?.message || err.message };
  }
};

export const putRequest = async ({ url, data }: { url: string; data?: any }) => {
  try {
    const res = await axiosInstance.put(url, data, { withCredentials: true });
    return res.data;
  } catch (err: any) {
    return { error: true, message: err.response?.data?.message || err.message };
  }
};

export const deleteRequest = async (url: string) => {
  try {
    const res = await axiosInstance.delete(url, { withCredentials: true });
    return res.data;
  } catch (err: any) {
    return { error: true, message: err.response?.data?.message || err.message };
  }
};

export { baseUrl };
